body, h1, h2, h3, h4, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
}

button, input {
  border: none;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #fafafa;
  background-color: #0c0b0b;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

a {
  color: currentColor;
}

button {
  cursor: pointer;
  font-family: inherit;
}

input {
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  fill: currentColor;
}

.container {
  max-width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 375px) {
